import util from '../../common/util.js'
export default {
	data() {
		return {
			reportList: [],
			page: 1,
			totalPages:1,
			contentText: {
			    contentdown: '查看更多',
			    contentrefresh: '加载中',
			    contentnomore: '没有更多',
			},
			ismore:false,
			status:'',
			currentId:''
		}
	},
	onLoad() {
	},
	onShow() {
		this.init()
	},
	methods: {
		init(){
			this.reportList = [],
			this.page = 1,
			this.totalPages = 1,
			this.requestData(1)
		},
		requestData(page) {
			this.$api.doctorReport({
				size: 10,
				page: page
			}).then(res => {
				console.log(res);
				if (res.status == "OK") {
					let arr = this.reportList
					let data = res.list
					this.totalPages = res.totalPages
					if(res.totalPages>1){
						this.ismore = true
					}
					data.forEach(item => {
						item.files = JSON.parse(item.files)
					})
					this.reportList = arr.concat(data)
				}
			}).catch(err => {
				console.log(err);
			})
		},
		gotoupload(){
			uni.navigateTo({
				url:'/pages/upload-report/upload-report?type=doctorReport'
			})
		},
		gotoDetail(id){
			uni.navigateTo({
				url:'/pages/look-report/look-report?type=doctorReport&id='+id
			})
		},
		deleteItem(id){
			this.currentId = id
			this.$refs['showtip'].open();
		},
		confirmDelete(){
			this.$api.deleteDoctorReport({
				id:this.currentId
			}).then(res=>{
				if(res.status == "OK"){
					this.page = 1
					this.reportList = []
					this.requestData(1)
					this.$refs['showtip'].close()
				}
			}).catch(err=>{
				console.log(err);
			})
			
		},
		cancel(){
			this.$refs['showtip'].close()
		},
		editReport(id){
			uni.navigateTo({
				url:'/pages/upload-report/upload-report?type=doctorReport&id='+id
			})
		},
	},
	onReachBottom() {
		console.log('onReachBottom')
		if(this.page<this.totalPages){
			this.status = 'loading'
			this.page++
			this.requestData(this.page)
		}else{
			this.status = ''
		}
		
	},
	filters: {
		formatDate: function(value) {
			return util.formatTimestamp(value, 'yyyy-MM-dd hh:mm')
		}
	}
}
